<template lang="html">
  <transition name="slide">
    <div class="user-center" ref="userCenter">
      <div class="header">
        <div class="icon-back" @click="back">
          <icon name="chevron-left" scale="1.25"></icon>
        </div>
        <div class="title">
          用户中心
        </div>
        <div class="icon-action" @click="action">
          <icon name="ellipsis-h" scale="1.5"></icon>
        </div>
      </div>
      <div class="tab-wrapper">
        <tab-btn :list="tabList" :index="tabIndex" @change="tabChange"></tab-btn>
      </div>
      <div class="content-wrapper">
        <scroller :refreshDelay="refreshDelay" :data="contentData" class="content" ref="scroller">
          <song-list
          :songs="playHistory"
          :showLoading="false"
          v-show="tabIndex == 0"
          @selectItem="selectSong"
          @edit="editPlayHistory"></song-list>
          <song-list
          :songs="favorite"
          :showLoading="false"
          v-show="tabIndex == 1"
          @selectItem="selectSong"
          @edit="editFavorite"></song-list>
          <disc-list
          :discs="collectDiscList"
          :showLoading="false"
          v-show="tabIndex == 2"
          :editable="true"
          @selectItem="selectDisc"
          @edit="editCollectDisc"
          ></disc-list>
          <disc-list
          :discs="myDiscList"
          :showLoading="false"
          v-show="tabIndex == 3"
          :editable="true"
          @selectItem="selectDisc"
          @edit="editMyDisc"
          ></disc-list>
        </scroller>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script>
  import * as atypes from '@/store/action-types'
  import { mapGetters, mapActions } from 'vuex'
  import { timeout } from 'assets/js/util'
  import PlayerMixin from '@/mixins/player-mixin'
  import { miniPlayerHeight } from 'assets/js/config'
  import Icon from 'vue-awesome/components/Icon'
  import 'vue-awesome/icons/chevron-left'
  import 'vue-awesome/icons/ellipsis-h'
  import Scroller from 'components/base/scroller/scroller'
  import SongList from 'components/base/song-list/song-list'
  import DiscList from 'components/base/disc-list/disc-list'
  import TabBtn from 'components/base/tab-btn/tab-btn'

  export default {
    name: 'user-center',
    components: { Icon, Scroller, SongList, TabBtn, DiscList },
    mixins: [PlayerMixin],
    data () {
      return {
        refreshDelay: 300,
        tabList: ['最近听的', '喜欢的歌', '收藏的歌单', '我的歌单'],
        tabIndex: 0
      }
    },
    computed: {
      ...mapGetters([
        'playHistory', 'favorite', 'collectDiscList', 'myDiscList'
      ]),
      contentData () {
        if (this.tabIndex === 0) return this.playHistory
        if (this.tabIndex === 1) return this.favorite
        if (this.tabIndex === 2) return this.collectDiscList
        if (this.tabIndex === 3) return this.myDiscList
        return []
      }
    },
    watch: {
      async tabIndex () {
        this.$refs.scroller.scrollTo(0, 0)
        await timeout(20)
        // 手动调用lazyload解决图片不加载的问题
        this.$Lazyload.lazyLoadHandler()
        this.$refs.scroller.refresh()
      }
    },
    methods: {
      ...mapActions({
        addSong: atypes.ADD_SONG_AND_PLAY,
        selectAndPlay: atypes.SELECT_AND_PLAY,
        randomPlay: atypes.RANDOM_PLAY,
        decachePlay: atypes.DECACHE_PLAY,
        decacheFavorite: atypes.DECACHE_FAVORITE,
        decacheCollect: atypes.DECACHE_COLLECT,
        decachePlayAll: atypes.DECACHE_PLAY_ALL,
        decacheFavoriteAll: atypes.DECACHE_FAVORITE_ALL,
        decacheCollectAll: atypes.DECACHE_COLLECT_ALL,
        createMyDisc: atypes.CREATE_MY_DISC,
        deleteMyDisc: atypes.DELETE_MY_DISC,
        deleteMyDiscAll: atypes.DELETE_MY_DISC_ALL,
        addSongToMyDisc: atypes.ADD_SONG_TO_MY_DISC,
        removeSongFromMyDisc: atypes.REMOVE_SONG_FROM_MY_DISC
      }),
      back () {
        this.$router.back()
      },
      tabChange (index) {
        this.tabIndex = index
      },
      selectSong (song, index) {
        this.addSong(song)
      },
      selectDisc (disc) {
        this.$router.push(`/user/${disc.id}`)
      },
      editPlayHistory (event, song, index) {
        this.editSong(event, song, index, 'playHistory')
      },
      editFavorite (event, song, index) {
        this.editSong(event, song, index, 'favorite')
      },
      editSong (event, song, index, type = 'playHistory') {
        const { pageX, pageY, offsetX, offsetY, target } = event
        this.$popup.show({
          x: pageX - offsetX + target.clientWidth / 2,
          y: pageY - offsetY + target.clientHeight / 2,
          actions: [
            {
              desc: '添加到我的歌单',
              operation: () => {
                const actions = this.myDiscList.map(disc => {
                  return {
                    desc: disc.name,
                    operation: () => {
                      this.addSongToMyDisc({ disc, song })
                    }
                  }
                })
                this.$actionsheet.show({ title: '请选择要加入的歌单', actions })
              }
            },
            {
              desc: '移除',
              operation: () => {
                if (type === 'playHistory') {
                  this.decachePlay(song)
                } else if (type === 'favorite') {
                  this.decacheFavorite(song)
                }
              }
            }
          ]
        })
      },
      editCollectDisc (event, disc, index) {
        this.editDisc(event, disc, index, 'collect')
      },
      editMyDisc (event, disc, index) {
        this.editDisc(event, disc, index, 'my')
      },
      editDisc (event, disc, index, type = 'collect') {
        const { pageX, pageY, offsetX, offsetY, target } = event
        this.$popup.show({
          x: pageX - offsetX + target.clientWidth / 2,
          y: pageY - offsetY + target.clientHeight / 2,
          actions: [
            {
              desc: '移除',
              operation: () => {
                if (type === 'collect') {
                  this.decacheCollect(disc)
                } else if (type === 'my') {
                  this.deleteMyDisc(disc)
                }
              }
            }
          ]
        })
      },
      action () {
        let actions = []
        if (this.tabIndex === 0) {
          actions = [
            { desc: '播放全部', operation: () => { this.selectAndPlay({ index: 0, list: this.playHistory }) } },
            {
              desc: '清空',
              operation: () => {
                this.$dialog.confirm({
                  title: '确定要清空最近听的歌？',
                  confirm: () => {
                    this.decachePlayAll()
                    this.$toast.info('已清空')
                  }
                })
              }
            }
          ]
        } else if (this.tabIndex === 1) {
          actions = [
            { desc: '播放全部', operation: () => { this.selectAndPlay({ index: 0, list: this.favorite }) } },
            {
              desc: '清空',
              operation: () => {
                this.$dialog.confirm({
                  title: '确定要清空喜欢的歌？',
                  confirm: () => {
                    this.decacheFavoriteAll()
                    this.$toast.info('已清空')
                  }
                })
              }
            }
          ]
        } else if (this.tabIndex === 2) {
          actions = [
            {
              desc: '清空',
              operation: () => {
                this.$dialog.confirm({
                  title: '确定要清空收藏的歌单？',
                  confirm: () => {
                    this.decacheCollectAll()
                    this.$toast.info('已清空')
                  }
                })
              }
            }
          ]
        } else if (this.tabIndex === 3) {
          actions = [
            {
              desc: '创建歌单',
              operation: () => {
                this.$dialog.prompt({
                  title: '创建新歌单',
                  placeholder: '请输入新歌单名称',
                  confirm: name => {
                    this.createMyDisc(name)
                  }
                })
              }
            },
            {
              desc: '清空',
              operation: () => {
                this.$dialog.confirm({
                  title: '确定要清空我的歌单？',
                  confirm: () => {
                    this.deleteMyDiscAll()
                    this.$toast.info('已清空')
                  }
                })
              }
            }
          ]
        }
        this.$actionsheet.show({
          title: '操作',
          actions
        })
      },
      handlePlaylist (playlist) {
        const bottom = playlist.length > 0 ? miniPlayerHeight : 0
        this.$refs.userCenter.style.bottom = bottom
        this.$refs.scroller.refresh()
      }
    }
  }
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .user-center {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background: @color-background;
    color: @color-text;
    display: flex;
    flex-direction: column;
    z-index: 10;
  }

  .header {
    position: relative;
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin: 5px auto;
    text-align: center;
    font-size: @font-size-large;
    .icon-back, .icon-action {
      .extend-click();
      color: @color-theme;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      line-height: 0;
    }
    .icon-back {
      left: 15px;
    }
    .icon-action {
      right: 15px;
    }
  }

  .tab-wrapper {
    padding: 5px 20px 20px;
    font-size: 14px;
  }

  .content-wrapper {
    flex: 1;
    overflow: hidden;
    .content {
      height: 100%;
    }
  }

  .song-list {
    padding: 0 20px;
    box-sizing: border-box;
  }

  .slide-enter-active, .slide-leave-active {
    transition: all .3s;
  }
  .slide-enter, .slide-leave-to {
    transform: translate(100%, 0);
  }
</style>



