<template lang="html">
  <div class="disc-list">
    <div class="loading-wrapper" v-show="discs.length == 0 && showLoading">
      <loading :scale="1.5"></loading>
    </div>
    <ul>
      <li v-for="(disc, index) in discs" class="item" @click="selectDisc(disc)">
        <div class="icon">
          <img width="60" height="60" v-lazy="disc.imgurl || defaultIcon">
        </div>
        <div class="text">
          <h2 class="name" v-html="disc.creator"></h2>
          <p class="desc" v-html="disc.name"></p>
        </div>
        <div class="edit" v-show="editable" @click.stop="edit($event, disc, index)">
          <icon name="ellipsis-v" scale="1.25"></icon>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Loading from 'components/base/loading/loading3'
import Icon from 'vue-awesome/components/Icon'
import 'vue-awesome/icons/ellipsis-v'
const defaultIcon = require('assets/images/default_disc.jpg')

export default {
  name: 'disc-list',
  data () {
    return {
      defaultIcon
    }
  },
  props: {
    discs: {
      type: Array,
      default: () => []
    },
    showLoading: {
      type: Boolean,
      default: true
    },
    editable: {
      type: Boolean,
      default: false
    }
  },
  components: { Loading, Icon },
  methods: {
    selectDisc (disc, index) {
      this.$emit('selectItem', disc, index)
    },
    edit (event, disc, index) {
      this.$emit('edit', event, disc, index)
    }
  }
}
</script>

<style scoped lang="less">
  @import "~assets/less/variable";
  @import "~assets/less/mixin";

  .disc-list {
    .item {
      display: flex;
      box-sizing: border-box;
      align-items: center;
      padding: 0 20px 20px 20px;
      .icon {
        flex: 0 0 60px;
        width: 60px;
        padding-right: 20px;
      }
      .text {
        // display: flex;
        // flex-direction: column;
        // justify-content: center;
        flex: 1;
        line-height: 20px;
        overflow: hidden;
        font-size: @font-size-medium;
        .name {
          margin-bottom: 10px;
          color: @color-text;
        }
        .desc {
          color: @color-text-d;
        }
      }
      .edit {
        .extend-click();
        margin-left: 20px;
        color: #fff;
      }
    }
  }

  .loading-wrapper {
    margin-top: 10px;
    text-align: center;
    color: @color-theme;
  }

</style>



